import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: import(/* webpackChunkName: "home" */ '@/pages/HomePage')
    },
    {
      path: '/cart',
      name: 'cart',
      component: import(/* webpackChunkName: "cart" */ '@/pages/CartPage')
    },
    {
      path: '/product/:id',
      name: 'product',
      component: import(/* webpackChunkName: "product" */ '@/pages/ProductPage')
    }
  ]
})



